@import "./vars.scss";

.m-context-menu {
  outline: none;
  font-size: 12px;
  z-index: 1000;
  position: absolute;
  box-shadow: 0 2px 12px 0 $border-color;
  background-color: #fafafa;
  white-space: nowrap;
  min-width: 100px;
  border-radius: 2px;
  border: 1px solid $border-color;
  box-sizing: border-box;
  overflow: hidden;
  transition: 0.3s;
  .item {
    cursor: pointer;
    display: block;
    background-color: $text-bg-color;
    color: $text-color;
    padding: 5px 7px;
    font-family: "Segoe UI", Calibri, Thonburi, Arial, Verdana, sans-serif, "Mongolian Baiti", "Microsoft Yi Baiti", "Javanese Text";
    font-size: 9pt;
    user-select: none;
  }

  .item:hover {
    color: $text-color-hover;
    background-color: $theme-color-9;
  }
}




